/**/
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300;400;500;700&display=swap');
body{overflow-x: hidden;font-size: 14px;}
body *{font-family: 'Noto Sans KR' !important;font-weight: 400;color:#191919;}
a:hover{color:#003300;}

/* */
.loadingWrap{position: fixed;width: 100%;height: 100%;}
.loadingWrap h1{width:300px;height:300px;border-top:3px solid #a8c4e7;position: absolute;top: calc(50% - 150px);left: calc(50% - 150px);transform: translate(-50%, -50%);border-radius: 100%;animation: spin 2s infinite;}
@keyframes spin{
	from { transform: rotate(0deg);}
	to{transform: rotate(360deg);}
}

/*list */
.list .movieList{display:flex;flex-wrap: wrap;margin:0 -6px;}
.list .movieList > li{width:20%;padding:0 6px;box-sizing: border-box;margin-bottom: 60px;}
.list .movieList > li img{width:100%;}